document.addEventListener('DOMContentLoaded', function () {
    const themeSwitcher = document.querySelector('a[href="#"][data-name="Mode"]');
    const icon = themeSwitcher.querySelector('i');

    // Function to set the theme
    function setTheme(theme) {
        if (theme === 'dark') {
            document.body.classList.add('dark-mode');
            icon.classList.remove('fa-moon');
            icon.classList.add('fa-sun');
            localStorage.setItem('theme', 'dark');
        } else {
            document.body.classList.remove('dark-mode');
            icon.classList.remove('fa-sun');
            icon.classList.add('fa-moon');
            localStorage.setItem('theme', 'light');
        }
    }

    // Check for saved theme preference, or use OS preference
    const savedTheme = localStorage.getItem('theme');
    const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;

    if (savedTheme) {
        setTheme(savedTheme);
    } else if (prefersDark) {
        setTheme('dark');
    } else {
        setTheme('light');
    }

    // Listen for changes in OS preference
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', e => {
        // Only change the theme if the user hasn't manually set one
        if (!localStorage.getItem('theme')) {
            if (e.matches) {
                setTheme('dark');
            } else {
                setTheme('light');
            }
        }
    });

    // Handle theme switching
    if (themeSwitcher) {
        themeSwitcher.addEventListener('click', function (e) {
            e.preventDefault();
            // When user manually switches, we use the opposite of the currently set theme
            const currentThemeIsDark = document.body.classList.contains('dark-mode');
            if (currentThemeIsDark) {
                setTheme('light');
            } else {
                setTheme('dark');
            }
        });
    }
});